import React, { useEffect, useRef, useState } from "react";
import axios from "axios";
import "./style.css";
import { toast, ToastContainer } from "react-toastify";
import "../CSS/MedicalHistoryAndInsurance.css";
import { FaTimes } from "react-icons/fa";

const MedicalHistoryLayer = () => {
  const [historyItems, setHistoryItems] = useState([
    {
      issue: "",
      documents: [],
    },
  ]);
  const [existingRecords, setExistingRecords] = useState([]);
  const [loading, setLoading] = useState(true);
  const userId = localStorage.getItem("userId"); // make sure you stored this at login
   const [showDeleteModal, setShowDeleteModal] = useState(false);
       const [recordToDelete, setRecordToDelete] = useState(null);

  const handleIssueChange = (index, value) => {
    const updated = [...historyItems];
    updated[index].issue = value;
    setHistoryItems(updated);
  };

  const fileRefs = useRef([]);

  const handleFileChange = (index, files) => {
    const file = files[0]; // only take first file

    if (file) {
      // file.size is in bytes. 1 MB = 1024 * 1024 bytes
      const maxSize = 5 * 1024 * 1024; // 5 MB in bytes

      if (file.size > maxSize) {
        alert(
          `File size should not exceed 5 MB. Your file is ${(
            file.size /
            (1024 * 1024)
          ).toFixed(2)} MB`
        );
        if (fileRefs.current[index]) {
          fileRefs.current[index].value = ""; // clear file input
        }
        return;
      }

      // Allowed formats
      const allowedFormats = /(\.jpg|\.jpeg|\.png|\.pdf|\.doc|\.docx)$/i;
      if (!allowedFormats.exec(file.name)) {
        toast.error(
          "File should be in .jpeg | .jpg | .png | .pdf | .doc | .docx format"
        );
        if (fileRefs.current[index]) {
          fileRefs.current[index].value = ""; // clear file input
        }
        return;
      }
    }

    // âœ… Store only ONE file (replace old file instead of appending)
    const updated = [...historyItems];
    updated[index].documents = file ? [file] : [];
    setHistoryItems(updated);
  };

  const handleRemoveFile = (issueIndex, fileIndex) => {
    const updated = [...historyItems];
    updated[issueIndex].documents = updated[issueIndex].documents.filter(
      (_, i) => i !== fileIndex
    );
    setHistoryItems(updated);
  };

  const addNewHistoryItem = () => {
    setHistoryItems((prev) => [
      ...prev,
      {
        issue: "",
        documents: [],
      },
    ]);
  };

  const removeHistoryItem = (index) => {
    setHistoryItems((prev) => {
      if (prev.length === 1) {
        // âœ… Reset the only record instead of removing
        return [{ issue: "", documents: [] }];
      }
      return prev.filter((_, i) => i !== index);
    });

    // âœ… Also reset file input if it's the last item
    if (fileRefs.current[index]) {
      fileRefs.current[index].value = "";
    }
  };

  // Fetch existing records on component mount

  const fetchMedicalHistory = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/medical-history`
      );
      setExistingRecords(res.data || []);
    } catch (error) {
      console.error("Error fetching medical history:", error);
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    fetchMedicalHistory();
  }, [userId]);
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();

      // Append each issue and its documents
      historyItems.forEach((item, i) => {
        formData.append("condition", item.issue);
        item.documents.forEach((file) => {
          formData.append("document", file); // must match multer field name
        });
      });

      const res = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/medical-history`,
        formData,
        {
          headers: { "Content-Type": "multipart/form-data" },
        }
      );

      toast.success("Medical history saved successfully!");
      setHistoryItems([{ issue: "", documents: [] }]); // reset form
      fileRefs.current.forEach((ref) => {
        if (ref) ref.value = ""; // clears the file input
      });
      fetchMedicalHistory();
    } catch (error) {
      console.error(error);
      toast.error("Error saving medical history.");
    }
  };

const [showInfoModal,setShowInfoModal] = useState(false)
  const modalRef = useRef(null);
  
    useEffect(() => {
      // Handle click outside
      const handleClickOutside = (event) => {
        if (
          modalRef.current &&
          !modalRef.current.contains(event.target) &&
          showInfoModal
        ) {
          showInfoModal(false);
        }
      };
  
      document.addEventListener("mousedown", handleClickOutside);
  
      // ðŸ”‘ Scroll lock logic
      if (showInfoModal) {
        document.body.style.overflow = "hidden"; // Disable background scroll
      } else {
        document.body.style.overflow = "auto"; // Enable background scroll
      }
  
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
        document.body.style.overflow = "auto"; // Cleanup when component unmounts
      };
    }, [showInfoModal]);
  
      const handleDeleteClick = (record) => {
      setRecordToDelete(record);
      setShowDeleteModal(true);
        setShowInfoModal(true);
    };
  
      const handleDeleteConfirm = async () => {
      if (!recordToDelete) return;
      
 try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/medical-history`,
        { data: { condition: recordToDelete.condition, document: recordToDelete.document } }
      );

      // Update UI by removing the deleted record
      setExistingRecords(
        existingRecords.filter(
          (r) =>
            r.condition !== recordToDelete.condition || r.document !== recordToDelete.document
        )
      );
      toast.success("Medical Record deleted successfully");
    } catch (error) {
      console.error("Error deleting record:", error);
      toast.error("Failed to delete record");
    }
    finally {
      // Close modal
      setShowDeleteModal(false);
        setShowInfoModal(false);
      setRecordToDelete(null);
    }
    };
  
    // Close delete modal
    const handleDeleteCancel = () => {
      setShowDeleteModal(false);
        setShowInfoModal(false);
      setRecordToDelete(null);
    };

  return (
    <div className="container py-4">
      <div className="row">
        {/* Form Section - First Half */}
        <div className="col-lg-12 pe-lg-4 mb-5 mb-lg-0 px-4">
          <div className="card shadow-sm">
            <div className="card-header bg-black text-white">
              <h5 className="mb-0 text-white">Add Medical History</h5>
            </div>
            <div className="card-body">
              <form onSubmit={handleSubmit}>
                {historyItems.map((item, index) => (
                  <div key={index} className="mb-4 border-bottom pb-3">
                 <div className="row g-3 align-items-end medical-history">
           
  {/* Health Issue / Condition */}
  <div className="col-md-5">
    <label className="form-label fw-bold">Health Issue / Condition</label>
    <input
      type="text"
      className="form-control medical-input"
      value={item.issue}
      onChange={(e) => handleIssueChange(index, e.target.value)}
      placeholder="e.g. Diabetes, Hypertension"
      required
    />
  </div>

  {/* Upload Documents */}
  <div className="col-md-5">
    <label className="form-label fw-bold">Upload Documents</label>
    <input
      type="file"
      className="form-control medical-input"
      ref={(el) => (fileRefs.current[index] = el)}
      onChange={(e) => handleFileChange(index, e.target.files)}
      accept=".pdf,.doc,.docx,.jpg,.jpeg,.png"
      required
    />
  </div>

  {/* Remove button inline */}
  <div className="col-md-2 d-grid">
    <button
      type="button"
      className="btn btn-outline-danger remove-btn-medical"
      style={{ height: "40px" }}
      onClick={() => removeHistoryItem(index)}
    >
      <i className="fas fa-trash-alt me-2"></i>Remove
    </button>
  </div>
  
</div>

                  </div>
                ))}

                {/* Add and Save buttons */}
                <div className="d-flex flex-column flex-md-row justify-content-between gap-2">
                  <button
                    type="button"
                    className="btn btn-success w-md-auto"
                    onClick={addNewHistoryItem}
                  >
                    <i className="fas fa-plus me-2"></i>
                    Add Another Issue
                  </button>

                  <button type="submit" className="btn btn-primary  w-md-auto">
                    <i className="fas fa-save me-2"></i>
                    Save Medical History
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>

        {/* Records Section - Second Half */}
        <div className="col-lg-12 ps-lg-4 my-4 px-4">
          <div className="card shadow-sm">
            <div className="card-header bg-black text-white">
              <h5 className="mb-0 text-white">Your Medical History Records</h5>
            </div>
            <div className="card-body">
              {loading ? (
                <div className="text-center py-4">
                  <div className="spinner-border text-primary" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </div>
                </div>
              ) : existingRecords.length === 0 ? (
                <div className="alert alert-info">
                  No medical history records found.
                </div>
              ) : (
                <div className="table-responsive">
                  <table className="table">
                    <thead>
                      <tr>
                        <th>Condition</th>
                        <th>Document</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {existingRecords.map((record, index) => (
                        <tr
                          key={`${record.condition}-${index}`}
                          className="hover-row"
                        >
                          <td
                            style={{
                              color: "#000424",
                            }}
                          >
                            {record.condition}
                          </td>
                          <td>
                            <a
                              href={record.document}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="btn btn-sm btn-outline-primary"
                            >
                              <i className="fas fa-eye me-1"></i> View
                            </a>
                          </td>
                          <td>
                            <button
                               onClick={() => handleDeleteClick(record)}
                              className="btn btn-sm btn-outline-danger"
                            >
                              <i className="fas fa-trash-alt"></i> Delete
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
 <div 
        className={`modal fade ${showDeleteModal ? 'show d-block' : ''}`} 
        style={{ backgroundColor: showDeleteModal ? 'rgba(0,0,0,0.5)' : 'transparent' }}
        tabIndex="-1"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Confirm Delete</h5>
              <button 
          type="button" 
          className="btn p-1 position-absolute  end-0 m-3"
          onClick={handleDeleteCancel}
          style={{ 
            background: 'transparent',
            border: 'none',
            color: '#6c757d',
            fontSize: '1.2rem',
            // marginTop:'12px'
          }}
        >
          <FaTimes />
        </button>
            </div>
            <div className="modal-body">
              <p>Are you sure you want to delete this medical record?</p>
              {recordToDelete && (
                <p className="fw-bold">Medical Record: {recordToDelete.condition}</p>
              )}
              <p className="text-danger">This action cannot be undone.</p>
            </div>
            <div className="modal-footer">
              <button 
                type="button" 
                className="btn btn-secondary" 
                onClick={handleDeleteCancel}
              >
                Cancel
              </button>
              <button 
                type="button" 
                className="btn btn-danger" 
                onClick={handleDeleteConfirm}
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      </div>

      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default MedicalHistoryLayer;
